.mainArticle {
  max-width: 60ch;
  position: relative;
  grid-area: 2/2/3/3;
}
.mainArticle > p:not(:has(img), .date) {
  text-indent: 0.8em;
}
article {
  font-size: var(--font-S);
  text-align: justify;
}
.articleTitle {
  display: inline;
  width: 100%;

  h2 {
    font-size: calc(var(--font-S) + 0.2rem);
    line-height: calc(var(--font-S) + 1rem);
    color: #73383e;
    text-align: center;
    /* background-image: url("../../elements/multimedia/titleUnderline.png");
    background-repeat: no-repeat no-repeat;
    background-position: 0 100%; */
    padding: 0 15% 3% 10%;
    &::after {
      position: absolute;
      content: "";
      width: 75%;
      height: 100%;
      top: 0;
      left: 10%;
      background-image: url("../../elements/multimedia/titleUnderline.png");
      background-repeat: no-repeat no-repeat;
      background-position: 0 100%;
    }
  }
  .date {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 85%;
    font-size: calc(var(--font-S) - 0.5rem);
    color: #73383e;
    white-space: pre-line;
    text-align: right;
  }
}

.articleLi::after {
  content: url("../../elements/multimedia/break-pomiedzy-newsami.png");
  display: block;
  width: 100%;
  text-align: center;
  margin: 1.5rem 0;
}
.ornamentLetter {
  float: left;
  height: 5.4rem;
  margin-right: 0.3rem;
}
.articleIMG {
  display: inline-block;
  max-width: 100%;
  font-style: oblique;
  text-align: center;
}
.newsArticleHiddenShowButton {
  width: 100%;
  height: 2rem;
  justify-content: end;
  & > img {
    padding-left: 3ch;
  }
}
.hiddenNews {
  display: block;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  position: relative;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 0 !important;
  -webkit-line-clamp: 10;
}
.codeElement {
  width: 100%;
  padding: 0 10%;
}
