#root {
  width: 100%;
  min-height: 100dvh;
  padding-bottom: 6vh;
  display: grid;
  grid-template-columns: 1fr repeat(3, 25rem) 1fr;
  grid-template-rows: 20rem auto;
  gap: 0 3em;
  z-index: 1;
}
#root::before {
  content: "";
  background-image: url("../../../public/multimedia/tlo-tyl1-czarne.png");
  background-size: cover;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.showAndHideNavButton,
.letterContein {
  display: none;
}
header {
  grid-area: 1 / 2 / 2 / 5;
  position: relative;
}
nav {
  position: sticky;
  grid-area: 2 / 2 / 3 / 3;
  position: -webkit-sticky;
  top: 5vh;
  height: 90vh;
}
main {
  grid-area: 2 / 3 / 3 / 5;
}
@media (max-width: 90rem) {
  #root {
    grid-template: 200px auto / 1fr repeat(3, 16em) 1fr;
    gap: 0 1em;
    --font-S: 1.4rem;
  }
}

@media (max-width: calc(50rem + 60px)) {
  #root {
    grid-template: 150px auto / 1fr 10rem 10rem 10rem 1fr;
    min-width: min-content;
    position: relative;
    --font-S: 1.2rem;
  }
  nav {
    height: calc(100dvh - 70px);
    width: 350px;
    max-width: 100vw;
    top: 70px;
    position: fixed;
    padding-left: 40px;
    left: -500px;
    z-index: 2;
    transition: transform 1s ease;
  }

  .hiddenNav {
    transform: translate(500px);
  }
  #root:has(nav > .showAndHideNavButton.closeNav) > main::after {
    position: fixed;
    content: "";
    width: 100vw;
    height: 100vh;
    background-color: #00000066;
    left: 0;
    top: 0;
  }
  main {
    grid-area: 2 / 2 / 3 / 5;
  }
  .letterContein {
    display: block;
  }
  .showAndHideNavButton {
    position: absolute;
    z-index: 5;
    height: 5rem;
    top: -70px;
    left: 500px;
    display: flex;
    padding-left: 15vw;
    transition: transform 1s ease;
    & > div {
      top: 0.1rem;
      left: 0;
      position: absolute;
      width: 70px;
      aspect-ratio: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background-color: #d3c18d99;
      border-radius: 50%;
      border: #73383e solid 10px;
    }
  }
  .showAndHideNavButton.closeNav {
    transform: translate(-500px);
  }
}
@media (max-width: 40em) {
  #root {
    grid-template: 150px auto / 1fr 90% 1fr;
    gap: 0;
    --font-S: 1rem;
  }
  main {
    grid-area: 2 / 2 / 3 / 3;
  }
}
